import React, {useState} from "react";
import {X, AlertTriangle, Package, Store} from "lucide-react";
import StarRating from "./StarRating";

interface RatingModalProps {
	type: "product" | "seller";
	entityId: number;
	entityName: string;
	entityImage?: string;
	orderId: number;
	isOpen: boolean;
	onClose: () => void;
	onSubmit: (data: {
		rating: number;
		title?: string;
		comment?: string;
		entityId: number;
		orderId: number;
	}) => Promise<void>;
	onReport?: (data: {
		type: "product" | "seller";
		entityId: number;
		orderId: number;
		problemType: string;
		description: string;
	}) => Promise<void>;
}

/**
 * Modal para valorar productos o vendedores
 */
const RatingModal: React.FC<RatingModalProps> = ({
	type,
	entityId,
	entityName,
	entityImage,
	orderId,
	isOpen,
	onClose,
	onSubmit,
	onReport,
}) => {
	// Estados del formulario de valoraci√≥n
	const [rating, setRating] = useState<number>(0);
	const [title, setTitle] = useState<string>("");
	const [comment, setComment] = useState<string>("");
	const [isSubmitting, setIsSubmitting] = useState<boolean>(false);

	// Estados para el modo reporte
	const [isReportMode, setIsReportMode] = useState<boolean>(false);
	const [problemType, setProblemType] = useState<string>("");
	const [problemDescription, setProblemDescription] = useState<string>("");

	// Estado para confirmaci√≥n de valoraci√≥n negativa
	const [showConfirmation, setShowConfirmation] = useState<boolean>(false);

	// Si el modal no est√° abierto, no lo renderizamos
	if (!isOpen) return null;

	// Reset de los formularios
	const resetForm = () => {
		setRating(0);
		setTitle("");
		setComment("");
		setIsReportMode(false);
		setProblemType("");
		setProblemDescription("");
		setShowConfirmation(false);
	};

	// Manejar cierre del modal
	const handleClose = () => {
		resetForm();
		onClose();
	};

	// Manejar env√≠o de valoraci√≥n
	const handleSubmit = async () => {
		// Validaci√≥n: Asegurarse de que haya una calificaci√≥n seleccionada
		if (rating === 0) {
			alert("Por favor selecciona una calificaci√≥n de 1 a 5 estrellas");
			return;
		}

		// Si la valoraci√≥n es baja (1-2 estrellas) y no estamos en modo confirmaci√≥n
		if (rating <= 2 && !showConfirmation) {
			setShowConfirmation(true);
			return;
		}

		try {
			setIsSubmitting(true);

			await onSubmit({
				rating,
				title: title.trim() || undefined,
				comment: comment.trim() || undefined,
				entityId,
				orderId,
			});

			resetForm();
			onClose();
		} catch (error) {
			console.error("Error al enviar valoraci√≥n:", error);
		} finally {
			setIsSubmitting(false);
		}
	};

	// Manejar env√≠o de reporte
	const handleReportSubmit = async () => {
		if (!onReport) return;

		// Validaci√≥n: Asegurarse de que haya un tipo de problema y descripci√≥n
		if (!problemType) {
			alert("Por favor selecciona el tipo de problema");
			return;
		}

		if (!problemDescription.trim()) {
			alert("Por favor describe el problema");
			return;
		}

		try {
			setIsSubmitting(true);

			await onReport({
				type,
				entityId,
				orderId,
				problemType,
				description: problemDescription,
			});

			resetForm();
			onClose();
		} catch (error) {
			console.error("Error al enviar reporte:", error);
		} finally {
			setIsSubmitting(false);
		}
	};

	// Alternar entre modos de valoraci√≥n y reporte
	const toggleReportMode = () => {
		setIsReportMode(!isReportMode);
		setShowConfirmation(false);
	};

	// Log para depuraci√≥n
	console.log("Estado actual de rating:", rating);

	return (
		<div className="fixed inset-0 z-50 overflow-y-auto">
			{/* Overlay oscuro */}
			<div
				className="fixed inset-0 bg-black bg-opacity-50 transition-opacity"
				onClick={handleClose}
			></div>

			{/* Contenido del modal */}
			<div className="flex min-h-screen items-center justify-center p-4">
				<div className="relative bg-white rounded-lg shadow-xl w-full max-w-md mx-auto">
					{/* Bot√≥n de cierre */}
					<button
						onClick={handleClose}
						className="absolute top-3 right-3 text-gray-400 hover:text-gray-500"
						disabled={isSubmitting}
					>
						<X size={24} />
					</button>

					{/* T√≠tulo del modal */}
					<div className="px-6 py-4 border-b border-gray-200">
						<h3 className="text-lg font-medium text-gray-900 flex items-center">
							{isReportMode ? (
								<>
									<AlertTriangle className="mr-2 text-orange-500" size={20} />
									Reportar problema
								</>
							) : (
								<>
									{type === "product" ? (
										<Package className="mr-2 text-blue-500" size={20} />
									) : (
										<Store className="mr-2 text-green-500" size={20} />
									)}
									Valorar {type === "product" ? "producto" : "vendedor"}
								</>
							)}
						</h3>
					</div>

					<div className="px-6 py-4">
						{/* Informaci√≥n del producto/vendedor */}
						<div className="flex items-center mb-4">
  <div className="w-16 h-16 bg-gray-200 rounded-md flex items-center justify-center mr-4 relative">
    {entityImage ? (
      <>
        <img
          src={entityImage}
          alt={entityName}
          className="w-16 h-16 object-cover rounded-md"
          onError={(e) => {
            const target = e.target as HTMLImageElement;
            target.style.display = 'none';
            const parent = target.parentElement;
            if (parent) {
              parent.querySelector('.fallback-icon')?.classList.remove('hidden');
            }
          }}
        />
        {type === "product" ? (
          <Package size={24} className="text-gray-400 fallback-icon hidden absolute" />
        ) : (
          <Store size={24} className="text-gray-400 fallback-icon hidden absolute" />
        )}
      </>
    ) : (
      <>
        {type === "product" ? (
          <Package size={24} className="text-gray-400" />
        ) : (
          <Store size={24} className="text-gray-400" />
        )}
      </>
    )}
  </div>
  <div>
    <h4 className="font-medium text-gray-900">{entityName}</h4>
    <p className="text-sm text-gray-500">Pedido: #{orderId}</p>
  </div>
</div>


						{/* Formulario de Valoraci√≥n */}
						{!isReportMode && !showConfirmation && (
							<div className="space-y-4">
								<div>
									<label className="block text-sm font-medium text-gray-700 mb-1">
										Tu valoraci√≥n
									</label>
									<StarRating
										value={rating}
										onChange={(value) => {
											console.log("Valor de StarRating seleccionado:", value);
											setRating(value);
										}}
										size="large"
										required
									/>
								</div>

								<div>
									<label
										htmlFor="rating-title"
										className="block text-sm font-medium text-gray-700 mb-1"
									>
										T√≠tulo (opcional)
									</label>
									<input
										id="rating-title"
										type="text"
										className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-primary-500 focus:border-primary-500"
										placeholder="Resume tu experiencia"
										value={title}
										onChange={(e) => setTitle(e.target.value)}
										maxLength={100}
									/>
								</div>

								<div>
									<label
										htmlFor="rating-comment"
										className="block text-sm font-medium text-gray-700 mb-1"
									>
										Comentario (opcional)
									</label>
									<textarea
										id="rating-comment"
										className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-primary-500 focus:border-primary-500"
										placeholder="Comparte tu experiencia con este producto"
										value={comment}
										onChange={(e) => setComment(e.target.value)}
										maxLength={500}
										rows={4}
									/>
								</div>
							</div>
						)}

						{/* Formulario de Reporte de Problema */}
						{isReportMode && !showConfirmation && (
							<div className="space-y-4">
								<div>
									<label
										htmlFor="problem-type"
										className="block text-sm font-medium text-gray-700 mb-1"
									>
										Tipo de problema
									</label>
									<select
										id="problem-type"
										className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-primary-500 focus:border-primary-500 bg-white"
										value={problemType}
										onChange={(e) => setProblemType(e.target.value)}
										required
									>
										<option value="">Selecciona el tipo de problema</option>
										{type === "product" ? (
											<>
												<option value="not_received">
													Producto no recibido
												</option>
												<option value="damaged">
													Producto da√±ado/defectuoso
												</option>
												<option value="different">
													Producto diferente al descrito
												</option>
												<option value="incomplete">Producto incompleto</option>
											</>
										) : (
											<>
												<option value="communication">
													Problemas de comunicaci√≥n
												</option>
												<option value="shipping">Problemas de env√≠o</option>
												<option value="customer_service">
													Mala atenci√≥n al cliente
												</option>
											</>
										)}
										<option value="other">Otro problema</option>
									</select>
								</div>

								<div>
									<label
										htmlFor="problem-description"
										className="block text-sm font-medium text-gray-700 mb-1"
									>
										Descripci√≥n del problema
									</label>
									<textarea
										id="problem-description"
										className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-primary-500 focus:border-primary-500"
										placeholder="Describe el problema en detalle"
										value={problemDescription}
										onChange={(e) => setProblemDescription(e.target.value)}
										rows={4}
										required
									/>
								</div>
							</div>
						)}

						{/* Confirmaci√≥n de valoraci√≥n negativa */}
						{showConfirmation && (
							<div className="bg-yellow-50 p-4 rounded-md">
								<div className="flex items-start">
									<AlertTriangle
										className="text-yellow-600 mt-0.5 mr-3"
										size={20}
									/>
									<div>
										<h4 className="font-medium text-yellow-800">
											¬øEst√°s seguro de enviar una valoraci√≥n baja?
										</h4>
										<p className="text-sm text-yellow-700 mt-1">
											Has calificado este{" "}
											{type === "product" ? "producto" : "vendedor"} con{" "}
											{rating} {rating === 1 ? "estrella" : "estrellas"}.
											¬øQuieres continuar o prefieres reportar un problema
											espec√≠fico?
										</p>
									</div>
								</div>
							</div>
						)}
					</div>

					{/* Pie del modal con botones de acci√≥n */}
					<div className="px-6 py-4 border-t border-gray-200 flex justify-between">
						{/* Bot√≥n izquierdo (var√≠a seg√∫n el contexto) */}
						<div>
							{!showConfirmation && (
								<button
									type="button"
									onClick={toggleReportMode}
									className="text-sm text-gray-600 hover:text-gray-800 flex items-center"
									disabled={isSubmitting}
								>
									{isReportMode ? (
										<>Volver a valoraci√≥n</>
									) : (
										<>
											<AlertTriangle size={14} className="mr-1" />
											Reportar problema
										</>
									)}
								</button>
							)}
						</div>

						{/* Botones principales */}
						<div className="space-x-3">
							{/* Bot√≥n de cancelar */}
							<button
								type="button"
								onClick={handleClose}
								className="px-4 py-2 bg-gray-200 text-gray-800 rounded-lg hover:bg-gray-300"
								disabled={isSubmitting}
							>
								Cancelar
							</button>

							{/* Bot√≥n de acci√≥n principal */}
							{showConfirmation ? (
								<>
									<button
										type="button"
										onClick={() => setShowConfirmation(false)}
										className="px-4 py-2 bg-yellow-500 text-white rounded-lg hover:bg-yellow-600 focus:outline-none focus:ring-2 focus:ring-yellow-500"
										disabled={isSubmitting}
									>
										Modificar valoraci√≥n
									</button>
									<button
										type="button"
										onClick={handleSubmit}
										className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
										disabled={isSubmitting}
									>
										{isSubmitting ? "Enviando..." : "Enviar valoraci√≥n"}
									</button>
								</>
							) : (
								<button
									type="button"
									onClick={isReportMode ? handleReportSubmit : handleSubmit}
									className={`px-4 py-2 text-white rounded-lg focus:outline-none focus:ring-2 ${
										isReportMode
											? "bg-orange-600 hover:bg-orange-700 focus:ring-orange-500"
											: "bg-blue-600 hover:bg-blue-700 focus:ring-blue-500"
									}`}
									disabled={
										isSubmitting ||
										(isReportMode &&
											(!problemType || !problemDescription.trim())) ||
										(!isReportMode && rating === 0)
									}
								>
									{isSubmitting
										? "Enviando..."
										: isReportMode
											? "Enviar reporte"
											: "Enviar valoraci√≥n"}
								</button>
							)}
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

export default RatingModal;
